// Repository for managing submission data in Firestore
import { 
  collection, 
  addDoc, 
  getDocs, 
  doc, 
  updateDoc, 
  deleteDoc, 
  query, 
  where, 
  orderBy,
  Timestamp 
} from 'firebase/firestore';
import { db } from './firebase';

// Type definitions for submission data
export interface SubmissionData {
  id?: string;
  // Data Dasar Keluarga
  namaKepalaKeluarga: string;
  nomorKTP: string;
  alamat: string;
  jumlahAnggotaKeluarga: number;
  
  // Analisis Kondisi Ekonomi
  pendapatanPerBulan: number;
  pengeluaranPerBulan: number;
  statusKepemilikanRumah: 'Milik Sendiri' | 'Sewa' | 'Numpang';
  
  // Analisis Kondisi Kualitatif & Spiritual
  deskripsiKondisiRumah: string;
  deskripsiMasalah: string;
  keterlibatanSosial: 'Aktif' | 'Kurang Aktif' | 'Tidak Aktif';
  
  // Kesimpulan dan Rekomendasi Program
  catatanKesimpulan: string;
  rekomendasiProgram: 'Bantuan Tunai' | 'Bantuan Sembako' | 'Pelatihan Kerja';
  
  // System fields
  status: 'diproses' | 'diterima' | 'ditolak';
  submissionDate: Timestamp;
}

export interface SubmissionStats {
  diproses: number;
  diterima: number;
  ditolak: number;
  total: number;
}

const COLLECTION_NAME = 'submissions';

/**
 * Create a new submission in Firestore
 */
export const createSubmission = async (submissionData: Omit<SubmissionData, 'id' | 'submissionDate' | 'status'>): Promise<string> => {
  try {
    const newSubmission = {
      ...submissionData,
      status: 'diproses' as const,
      submissionDate: Timestamp.now()
    };
    
    const docRef = await addDoc(collection(db, COLLECTION_NAME), newSubmission);
    console.log('Submission created with ID:', docRef.id);
    return docRef.id;
  } catch (error) {
    console.error('Error creating submission:', error);
    throw new Error('Failed to create submission. Please try again.');
  }
};

/**
 * Get all submissions from Firestore
 */
export const getSubmissions = async (): Promise<SubmissionData[]> => {
  try {
    const q = query(
      collection(db, COLLECTION_NAME),
      orderBy('submissionDate', 'desc')
    );
    
    const querySnapshot = await getDocs(q);
    const submissions: SubmissionData[] = [];
    
    querySnapshot.forEach((doc) => {
      submissions.push({
        id: doc.id,
        ...doc.data()
      } as SubmissionData);
    });
    
    return submissions;
  } catch (error) {
    console.error('Error fetching submissions:', error);
    throw new Error('Failed to fetch submissions. Please try again.');
  }
};

/**
 * Update an existing submission
 */
export const updateSubmission = async (id: string, updateData: Partial<SubmissionData>): Promise<void> => {
  try {
    const submissionRef = doc(db, COLLECTION_NAME, id);
    await updateDoc(submissionRef, updateData);
    console.log('Submission updated successfully');
  } catch (error) {
    console.error('Error updating submission:', error);
    throw new Error('Failed to update submission. Please try again.');
  }
};

/**
 * Delete a submission from Firestore
 */
export const deleteSubmission = async (id: string): Promise<void> => {
  try {
    await deleteDoc(doc(db, COLLECTION_NAME, id));
    console.log('Submission deleted successfully');
  } catch (error) {
    console.error('Error deleting submission:', error);
    throw new Error('Failed to delete submission. Please try again.');
  }
};

/**
 * Get submission statistics for dashboard
 */
export const getSubmissionStats = async (): Promise<SubmissionStats> => {
  try {
    const allSubmissions = await getSubmissions();
    
    const stats = allSubmissions.reduce(
      (acc, submission) => {
        acc[submission.status]++;
        acc.total++;
        return acc;
      },
      { diproses: 0, diterima: 0, ditolak: 0, total: 0 }
    );
    
    return stats;
  } catch (error) {
    console.error('Error fetching submission stats:', error);
    throw new Error('Failed to fetch submission statistics. Please try again.');
  }
};

/**
 * Get submissions by status
 */
export const getSubmissionsByStatus = async (status: 'diproses' | 'diterima' | 'ditolak'): Promise<SubmissionData[]> => {
  try {
    const q = query(
      collection(db, COLLECTION_NAME),
      where('status', '==', status),
      orderBy('submissionDate', 'desc')
    );
    
    const querySnapshot = await getDocs(q);
    const submissions: SubmissionData[] = [];
    
    querySnapshot.forEach((doc) => {
      submissions.push({
        id: doc.id,
        ...doc.data()
      } as SubmissionData);
    });
    
    return submissions;
  } catch (error) {
    console.error('Error fetching submissions by status:', error);
    throw new Error('Failed to fetch submissions by status. Please try again.');
  }
};
