'use client';

import { useState, useEffect } from 'react';
import ProtectedLayout from '@/components/ProtectedLayout';
import { getSubmissionStats, SubmissionStats } from '@/lib/submissionRepository';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { Pie } from 'react-chartjs-2';

// Register Chart.js components
ChartJS.register(ArcElement, Tooltip, Legend);

export default function ReportsPage() {
  const [stats, setStats] = useState<SubmissionStats | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    fetchStats();
  }, []);

  const fetchStats = async () => {
    try {
      setLoading(true);
      const data = await getSubmissionStats();
      setStats(data);
    } catch (error: any) {
      console.error('Error fetching stats:', error);
      setError(error.message || 'Gagal memuat data laporan');
    } finally {
      setLoading(false);
    }
  };

  // Chart configuration
  const chartData = {
    labels: ['Diterima', 'Ditolak', 'Sedang Diproses'],
    datasets: [
      {
        data: stats ? [stats.diterima, stats.ditolak, stats.diproses] : [0, 0, 0],
        backgroundColor: [
          '#10B981', // Green for accepted
          '#EF4444', // Red for rejected
          '#3B82F6', // Blue for processing
        ],
        borderColor: [
          '#059669',
          '#DC2626',
          '#2563EB',
        ],
        borderWidth: 2,
        hoverBackgroundColor: [
          '#059669',
          '#DC2626',
          '#2563EB',
        ],
      },
    ],
  };

  const chartOptions = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        position: 'bottom' as const,
        labels: {
          padding: 20,
          font: {
            size: 14,
          },
        },
      },
      tooltip: {
        callbacks: {
          label: function(context: any) {
            const label = context.label || '';
            const value = context.parsed || 0;
            const total = stats?.total || 0;
            const percentage = total > 0 ? ((value / total) * 100).toFixed(1) : '0';
            return `${label}: ${value} (${percentage}%)`;
          },
        },
      },
    },
  };

  const StatCard = ({ 
    title, 
    count, 
    percentage, 
    bgColor, 
    textColor,
    icon 
  }: { 
    title: string; 
    count: number; 
    percentage: string;
    bgColor: string; 
    textColor: string;
    icon: string;
  }) => (
    <div className={`${bgColor} rounded-lg p-6 shadow-sm border`}>
      <div className="flex items-center justify-between">
        <div>
          <p className={`text-sm font-medium ${textColor} opacity-80`}>
            {title}
          </p>
          <p className={`text-3xl font-bold ${textColor} mt-2`}>
            {count}
          </p>
          <p className={`text-sm ${textColor} opacity-70 mt-1`}>
            {percentage}% dari total
          </p>
        </div>
        <div className={`text-4xl ${textColor} opacity-60`}>
          {icon}
        </div>
      </div>
    </div>
  );

  return (
    <ProtectedLayout>
      <div className="space-y-6">
        {/* Page Header */}
        <div>
          <h2 className="text-2xl font-bold text-gray-900">Laporan</h2>
          <p className="text-gray-600 mt-1">
            Visualisasi data dan statistik pengajuan bantuan sosial
          </p>
        </div>

        {/* Loading State */}
        {loading && (
          <div className="flex items-center justify-center py-12">
            <div className="text-center">
              <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600 mx-auto"></div>
              <p className="mt-2 text-gray-600">Memuat data laporan...</p>
            </div>
          </div>
        )}

        {/* Error State */}
        {error && (
          <div className="bg-red-50 border border-red-200 text-red-700 px-4 py-3 rounded-md">
            <p className="font-medium">Terjadi Kesalahan</p>
            <p className="text-sm mt-1">{error}</p>
            <button
              onClick={fetchStats}
              className="mt-2 text-sm text-red-800 hover:text-red-900 underline"
            >
              Coba Lagi
            </button>
          </div>
        )}

        {/* Statistics Cards */}
        {stats && !loading && (
          <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
            <StatCard
              title="Total Pengajuan"
              count={stats.total}
              percentage="100"
              bgColor="bg-gray-50"
              textColor="text-gray-700"
              icon="📊"
            />
            
            <StatCard
              title="Sedang Diproses"
              count={stats.diproses}
              percentage={stats.total > 0 ? ((stats.diproses / stats.total) * 100).toFixed(1) : '0'}
              bgColor="bg-blue-50"
              textColor="text-blue-700"
              icon="⏳"
            />
            
            <StatCard
              title="Diterima"
              count={stats.diterima}
              percentage={stats.total > 0 ? ((stats.diterima / stats.total) * 100).toFixed(1) : '0'}
              bgColor="bg-green-50"
              textColor="text-green-700"
              icon="✅"
            />
            
            <StatCard
              title="Ditolak"
              count={stats.ditolak}
              percentage={stats.total > 0 ? ((stats.ditolak / stats.total) * 100).toFixed(1) : '0'}
              bgColor="bg-red-50"
              textColor="text-red-700"
              icon="❌"
            />
          </div>
        )}

        {/* Chart Section */}
        {stats && stats.total > 0 && !loading && (
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            {/* Pie Chart */}
            <div className="bg-white rounded-lg p-6 shadow-sm border">
              <h3 className="text-lg font-semibold text-gray-900 mb-4">
                Distribusi Status Pengajuan
              </h3>
              <div className="h-80">
                <Pie data={chartData} options={chartOptions} />
              </div>
            </div>

            {/* Summary Table */}
            <div className="bg-white rounded-lg p-6 shadow-sm border">
              <h3 className="text-lg font-semibold text-gray-900 mb-4">
                Ringkasan Detail
              </h3>
              
              <div className="space-y-4">
                <div className="flex justify-between items-center p-3 bg-green-50 rounded-lg">
                  <div className="flex items-center">
                    <div className="w-4 h-4 bg-green-500 rounded-full mr-3"></div>
                    <span className="font-medium text-green-800">Diterima</span>
                  </div>
                  <div className="text-right">
                    <p className="font-bold text-green-800">{stats.diterima}</p>
                    <p className="text-sm text-green-600">
                      {stats.total > 0 ? ((stats.diterima / stats.total) * 100).toFixed(1) : '0'}%
                    </p>
                  </div>
                </div>

                <div className="flex justify-between items-center p-3 bg-red-50 rounded-lg">
                  <div className="flex items-center">
                    <div className="w-4 h-4 bg-red-500 rounded-full mr-3"></div>
                    <span className="font-medium text-red-800">Ditolak</span>
                  </div>
                  <div className="text-right">
                    <p className="font-bold text-red-800">{stats.ditolak}</p>
                    <p className="text-sm text-red-600">
                      {stats.total > 0 ? ((stats.ditolak / stats.total) * 100).toFixed(1) : '0'}%
                    </p>
                  </div>
                </div>

                <div className="flex justify-between items-center p-3 bg-blue-50 rounded-lg">
                  <div className="flex items-center">
                    <div className="w-4 h-4 bg-blue-500 rounded-full mr-3"></div>
                    <span className="font-medium text-blue-800">Sedang Diproses</span>
                  </div>
                  <div className="text-right">
                    <p className="font-bold text-blue-800">{stats.diproses}</p>
                    <p className="text-sm text-blue-600">
                      {stats.total > 0 ? ((stats.diproses / stats.total) * 100).toFixed(1) : '0'}%
                    </p>
                  </div>
                </div>

                <div className="border-t pt-3 mt-4">
                  <div className="flex justify-between items-center">
                    <span className="font-semibold text-gray-900">Total</span>
                    <span className="font-bold text-gray-900">{stats.total}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}

        {/* Additional Insights */}
        {stats && stats.total > 0 && !loading && (
          <div className="bg-white rounded-lg p-6 shadow-sm border">
            <h3 className="text-lg font-semibold text-gray-900 mb-4">
              Insight dan Analisis
            </h3>
            
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div>
                <h4 className="font-medium text-gray-900 mb-2">Tingkat Persetujuan</h4>
                <div className="space-y-2">
                  <div className="flex justify-between">
                    <span className="text-sm text-gray-600">Tingkat Penerimaan:</span>
                    <span className="text-sm font-medium text-green-600">
                      {stats.total > 0 ? ((stats.diterima / stats.total) * 100).toFixed(1) : '0'}%
                    </span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-sm text-gray-600">Tingkat Penolakan:</span>
                    <span className="text-sm font-medium text-red-600">
                      {stats.total > 0 ? ((stats.ditolak / stats.total) * 100).toFixed(1) : '0'}%
                    </span>
                  </div>
                </div>
              </div>

              <div>
                <h4 className="font-medium text-gray-900 mb-2">Status Pemrosesan</h4>
                <div className="space-y-2">
                  <div className="flex justify-between">
                    <span className="text-sm text-gray-600">Menunggu Review:</span>
                    <span className="text-sm font-medium text-blue-600">
                      {stats.diproses} pengajuan
                    </span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-sm text-gray-600">Sudah Diproses:</span>
                    <span className="text-sm font-medium text-gray-600">
                      {stats.diterima + stats.ditolak} pengajuan
                    </span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}

        {/* Empty State */}
        {stats && stats.total === 0 && !loading && (
          <div className="text-center py-12">
            <div className="text-gray-400 mb-4">
              <div className="w-16 h-16 mx-auto bg-gray-100 rounded-full flex items-center justify-center">
                <span className="text-2xl">📊</span>
              </div>
            </div>
            <h3 className="text-lg font-medium text-gray-900 mb-2">
              Belum Ada Data untuk Laporan
            </h3>
            <p className="text-gray-600 mb-4">
              Mulai dengan membuat asesmen baru untuk melihat laporan dan statistik.
            </p>
            <a
              href="/new-assessment"
              className="inline-flex items-center px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-colors"
            >
              Buat Asesmen Baru
            </a>
          </div>
        )}

        {/* Export Actions */}
        {stats && stats.total > 0 && !loading && (
          <div className="bg-white rounded-lg p-6 shadow-sm border">
            <h3 className="text-lg font-semibold text-gray-900 mb-4">
              Ekspor Data
            </h3>
            <div className="flex flex-wrap gap-3">
              <button
                onClick={() => window.print()}
                className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-colors"
              >
                Cetak Laporan
              </button>
              <button
                onClick={() => {
                  const dataStr = JSON.stringify(stats, null, 2);
                  const dataBlob = new Blob([dataStr], {type: 'application/json'});
                  const url = URL.createObjectURL(dataBlob);
                  const link = document.createElement('a');
                  link.href = url;
                  link.download = `laporan-bantuan-${new Date().toISOString().split('T')[0]}.json`;
                  link.click();
                }}
                className="px-4 py-2 bg-green-600 text-white rounded-md hover:bg-green-700 transition-colors"
              >
                Unduh Data JSON
              </button>
            </div>
          </div>
        )}
      </div>
    </ProtectedLayout>
  );
}
